import React, { Component } from 'react';
import ReactDOM from "react-dom";
import axios from 'axios';
import cookie from 'react-cookies';
import JSONPretty from 'react-json-pretty';
import Linkify from 'react-linkify';
import PropTypes from 'prop-types';
import ReactPaginate from 'react-paginate';
import '../App.css';
import 'react-json-pretty/themes/monikai.css';
import $ from 'jquery';
var JSONPrettyMon = require('react-json-pretty/dist/monikai');


const url = "https://gizdo2uwtj.execute-api.us-east-1.amazonaws.com/dev/getmessage";
window.React = React;



const Tabs = (props) => {
    var divStyle = {       
    };
    var divStysle = {
        backgroundColor: '#fff',
        display: "block",
        borderRadius: "3px",
        paddingTop : "3px",
        paddingBottom : "3px",
        paddingLeft : "5px"  ,
        flex: 1,
        flexDirection: 'row',
        wordWrap:"break-word",
        maxWidth:"680px"
    };

    var namedivStysle = {
        fontSize: '12px',
        color:'#555555'
    };
    
    return (
        <div id="message_text_ul">
        {props.tabs.map((tab) => (      
           <ul><span style={namedivStysle}>{tab.name}</span><br/><Linkify><span style={divStysle}>{tab.message}</span></Linkify></ul>            
     ))}
           </div>
 )
}


export default class His extends React.Component {
    static propTypes = {
        url: PropTypes.string.isRequired,
        author: PropTypes.string.isRequired,
        perPage: PropTypes.number.isRequired,
    };

    constructor(props) {
        super(props);

        this.state = {
            tabs: [],
            data: [],
            offset: 0,
            selected: 1,
            pageCount:39
        };
    }


     
    fetchData()
    {
        var urla = url+"?num=" + this.state.selected;
        axios.get(urla,{
            headers: { 'Access-Control-Allow-Origin': '*',"Access-Control-Allow-Credentials": true}
        }).then(res => {
         
            var tabsData = [];        
            for(var i = 0;i<res.data.length;i++)
            {
                var Item = res.data[i];
                var Item_message = Item["message"].S;                
                const data_list = Item_message.split("**");
                if(data_list.length>=3)
                {             
                    var str = data_list[1]+"**"+data_list[2]
                    const inputValue = Item_message;
                    const name_Value = inputValue.split("**")[1];
                    const message_Value = inputValue.split("**")[2];                        
                    tabsData.push({                name: name_Value,                message: message_Value            });
                }      
            }

            this.setState({tabs:tabsData});
            
        }).catch(err => {
            this.setState({
                json_response: null
            })
        }); 
    }

    handlePageClick = data => {
        let aselected = data.selected+1;
        let offset = Math.ceil(aselected * this.props.perPage);
        this.setState({selected:aselected});
        this.setState({ offset: offset }, () => {
            this.fetchData();
        });
    };


    componentDidMount() {
        this.fetchData();
    }


  
    render()
    {
        let className = 'bubble-text';
        return (
                 <div className={className}>
           {
               this.state.tabs.length ? (
                  <Tabs tabs={this.state.tabs} />
            ) : (null)
}

       <ReactPaginate
previousLabel={'previous'}
nextLabel={'next'}
breakLabel={'...'}
breakClassName={'break-me'}
pageCount={this.state.pageCount}
marginPagesDisplayed={2}
pageRangeDisplayed={5}
onPageChange={this.handlePageClick}
containerClassName={'pagination'}
subContainerClassName={'pages pagination'}
activeClassName={'active'}
/>
</div>


             )
}
}
